.MainGrid {
    display: grid;

    grid-template-areas: "firstColumn secondColumn";
    grid-template-columns: 614px 293px;

    max-width: 957px;
    margin: 100px auto;

    grid-gap: 35px;
}

.box {
    background: var(--background);
    border: 1px solid var(--border)
}

.suggestionBox {
    flex: 1;
    max-width: 293px;
    position: fixed;
    
}

.legend {
    margin: 6px 0px;
}

.legend p span{
    font-weight: 500;
}

