.container{
    display: flex;
    gap: 16px;
    padding: 15px;
}

.story-elements{
    cursor: pointer;
}

.user-elements div{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(#c82d8e, #f99d4c);
}

.img-user-story{
    border: 2px solid var(--background);
    width: 56px;
    height: 56px;
    border-radius: 50%;
    cursor: pointer;

}

.user-elements span{
    font-size: 13px;
    cursor: pointer;
}

